/* global console, document, Excel, Office */

Office.onReady((info) => {
  if (info.host === Office.HostType.Excel) {
    document.getElementById("app-body").style.display = "flex";
    document.getElementById("create-template").onclick = createTemplate; // 添加按钮事件
    document.getElementById("export-values").onclick = exportModifiedValues; // 绑定导出按钮事件
  }
});

export async function createTemplate() {
  try {
    await Excel.run(async (context) => {
      const sheetNames = ["BMS1_CalibGen", "BMS2_CalibGen"];

      for (const sheetName of sheetNames) {
        // 检查是否存在同名工作表
        const existingSheet = context.workbook.worksheets.getItemOrNullObject(sheetName);
        await context.sync();

        if (!existingSheet.isNullObject) {
          // 如果工作表存在，在任务窗格中动态提示用户是否覆盖
          const userResponse = await showTaskpaneMessage(`The sheet "${sheetName}" already exists. Do you want to overwrite it?`);
          if (userResponse === "yes") {
            existingSheet.delete(); // 删除现有工作表
          } else {
            console.log(`Skipped creating sheet "${sheetName}".`);
            continue; // 跳过创建此工作表
          }
        }

        // 创建新的工作表
        const sheet = context.workbook.worksheets.add(sheetName);
        let data;

        if (sheetName === "BMS1_CalibGen") {
          data = [
            ["Name", "Value", "Note"],
            ["TuneCAD_CCP", "Enable", "Set to Enable so TuneCAD can be used for calibration activities."],
            ["Calibration Ref(F182)", "A322", "Cxx_calib_nr"],
            ["Soft(F194)", "CL01", "Ctx_sw_name"],
            ["VPROG(F195)", "9110", "To be modified by E2CAD,Ctx_sw_version"],
            ["REF SW(Ref. Logiciel RSA)F012", "293861621R", "Ctx_refC,Ctx_RUC"],
            ["REF C ($F187)", "293A03498R", "Ctx_c_id1-Ctx_c_id16"],
            ["Diag. Version\n(F1A0)", "06", "Cxx_Vdiag"],
            ["Ref homologation F196", "HMLGT0434R", "Ctx_ObdApproval,Cxx_obd_cert_nr1 -Cxx_obd_cert_nr10"],
            ["BECM-B+EnergyCtrl", "EqualForAllProject", "Cxx_obd_ecu_name_1 to Cxx_obd_ecu_name_20"]
          ];
          sheet.getRange("A1:C10").values = data;
        } else if (sheetName === "BMS2_CalibGen") {
          data = [
            ["Name", "Value", "Note"],
            ["Calibration Ref(F182)", "A422", "Cxx_calib_nr"],
            ["Soft(F194)", "CL01", "Ctx_sw_name"],
            ["VPROG(F195)", "9110", "To be modified by E2CAD,Ctx_sw_version"],
            ["REF SW(Ref. Logiciel RSA)F012", "293861621R", "Ctx_refC,Ctx_RUC"],
            ["Ref A ($F191)", "293A09147R", "Ctx_refA"]
          ];
          sheet.getRange("A1:C6").values = data;
        }

        // 自动调整列宽和行高
        sheet.getUsedRange().format.autofitColumns();
        sheet.getUsedRange().format.autofitRows();
      }

      // 激活第一个工作表
      const firstSheet = context.workbook.worksheets.getItem(sheetNames[0]);
      firstSheet.activate();

      await context.sync();
    });
  } catch (error) {
    console.error(error);
  }
}

// 在任务窗格中动态显示提示信息
function showTaskpaneMessage(message) {
  return new Promise((resolve) => {
    const taskpaneContainer = document.getElementById("taskpane-message-container");
    taskpaneContainer.innerHTML = `
      <p>${message}</p>
      <button id="overwrite-yes" class="ms-Button">Yes</button>
      <button id="overwrite-no" class="ms-Button">No</button>
    `;

    // 添加点击事件处理程序
    document.getElementById("overwrite-yes").onclick = () => {
      fadeOutAndClear(taskpaneContainer); // 渐变消失并清空内容
      resolve("yes");
    };

    document.getElementById("overwrite-no").onclick = () => {
      fadeOutAndClear(taskpaneContainer); // 渐变消失并清空内容
      resolve("no");
    };
  });
}

// 渐变消失并清空内容
function fadeOutAndClear(element) {
  element.style.transition = "opacity 0.5s ease"; // 设置渐变效果
  element.style.opacity = "0"; // 设置透明度为 0
  setTimeout(() => {
    element.innerHTML = ""; // 清空内容
    element.style.opacity = "1"; // 恢复透明度
  }, 500); // 等待渐变完成后清空内容
}

export async function exportModifiedValues() {
  try {
    await Excel.run(async (context) => {
      // 获取当前活动工作表
      const sheet = context.workbook.worksheets.getActiveWorksheet();
      sheet.load("name"); // 加载工作表的名字
      await context.sync(); // 同步上下文

      const sheetName = sheet.name; // 获取当前工作表的名字
      console.log(`Active sheet name: ${sheetName}`);

      const usedRange = sheet.getUsedRange();
      usedRange.load("values"); // 加载单元格的值
      await context.sync();

      const data = usedRange.values; // 获取所有单元格的值
      const modifiedValues = [];

      if (sheetName === "BMS1_CalibGen") {
        // BMS1_CalibGen 的处理逻辑
        for (let i = 1; i < data.length; i++) { // 跳过第一行标题
          const name = data[i][0]; // 第一列
          let value = data[i][1]; // 第二列

          // 确保 value 是字符串类型
          if (value !== null && value !== undefined) {
            value = value.toString();
          } else {
            value = ""; // 如果 value 是 null 或 undefined，设置为空字符串
          }

          if (i === 1) { // 第一行逻辑
            const output = value === "Enable" ? "Cbx_ccp_ena =1;" : "Cbx_ccp_ena =0;";
            modifiedValues.push(output);
          } else if (i === 2) { // 第二行逻辑
            modifiedValues.push(`Cxx_calib_nr= hex2dec('${value}');`);
          } else if (i === 3) { // 第三行逻辑
            const asciiValues = value.split("").map((char) => char.charCodeAt(0)).join(" ");
            modifiedValues.push(`Ctx_sw_name=[${asciiValues}];% NPROG (F194 = ${value})`);
          } else if (i === 4) { // 第四行逻辑
            const asciiValues = value.split("").map((char) => char.charCodeAt(0)).join(" ");
            modifiedValues.push(`%Ctx_sw_version=[${asciiValues}]; %VPROG (F195 =${value}), Not in A2L`);
          } else if (i === 5) { // 第五行逻辑
            const asciiValues = value.split("").map((char) => char.charCodeAt(0)).join(" ");
            modifiedValues.push(`Ctx_refC=[${asciiValues}]; %REF SW(Ref. Logiciel RSA) F012 = F188 = ${value}`);
            modifiedValues.push(`Ctx_RUC =[${asciiValues}]; %REF SW(Ref. Logiciel RSA) F012 = F188 = ${value}`);
          } else if (i === 6) { // 第六行逻辑
            const asciiValues = value.split("").map((char) => char.charCodeAt(0));
            for (let j = 0; j < 16; j++) {
              const asciiValue = asciiValues[j] || 0; // 如果不足 16 个字符，补 0
              modifiedValues.push(`Ctx_c_id${j + 1}   =   ${asciiValue}   ;% REF C ($F187) = ${value}`);
            }
          } else if (i === 7) { // 第七行逻辑
            modifiedValues.push(`Cxx_Vdiag = ${parseInt(value, 16)};%Diag. Version (F1A0) is ${value}.`);
          } else if (i === 8) { // 第八行逻辑
            const asciiValues = value.split("").map((char) => char.charCodeAt(0)).join(" ");
            modifiedValues.push(`Ctx_ObdApproval = [${asciiValues}]; %Ref homologation F196 = ${value}`);
            const asciiArray = value.split("").map((char) => char.charCodeAt(0));
            for (let j = 0; j < 10; j++) {
              const asciiValue = asciiArray[j] || 0; // 如果不足 10 个字符，补 0
              modifiedValues.push(`Cxx_obd_cert_nr${j + 1}    =   ${asciiValue}   ;%Ref homologation F196 = ${value}`);
            }
          } else if (i === 9) { // 第九行逻辑
            const fixedValues = [
              66, 69, 67, 77, 45, 66, 43, 69, 110, 101, 114, 103, 121, 67, 116, 114, 108, 0, 0, 0
            ];
            for (let j = 0; j < fixedValues.length; j++) {
              modifiedValues.push(`Cxx_obd_ecu_name_${j + 1}  = ${fixedValues[j]}; % BECM-B+EnergyCtrl`);
            }
          }
        }
      } else if (sheetName === "BMS2_CalibGen") {
        // BMS2_CalibGen 的处理逻辑
        for (let i = 1; i < data.length; i++) { // 跳过第一行标题
          const name = data[i][0]; // 第一列
          let value = data[i][1]; // 第二列

          // 确保 value 是字符串类型
          if (value !== null && value !== undefined) {
            value = value.toString();
          } else {
            value = ""; // 如果 value 是 null 或 undefined，设置为空字符串
          }

          if (i === 1) { // 第一行逻辑
            modifiedValues.push(`Cxx_calib_nr= hex2dec('${value}');%Calibration Ref(F182)`);
          } else if (i === 2) { // 第二行逻辑
            const asciiValues = value.split("").map((char) => char.charCodeAt(0)).join(" ");
            modifiedValues.push(`Ctx_sw_name=[${asciiValues}];% NPROG (F194 = ${value})`);
          } else if (i === 3) { // 第三行逻辑
            const asciiValues = value.split("").map((char) => char.charCodeAt(0)).join(" ");
            modifiedValues.push(`%Ctx_sw_version=[${asciiValues}]; %VPROG (F195 =${value}),not in A2L.`);
          } else if (i === 4) { // 第四行逻辑
            const asciiValues = value.split("").map((char) => char.charCodeAt(0)).join(" ");
            modifiedValues.push(`Ctx_refC=[${asciiValues}]; %F012 = ${value}  Ref envoyé par Raoul DROUSSE`);
            modifiedValues.push(`Ctx_RUC =[${asciiValues}]; %F012 = ${value}  Ref envoyé par Raoul DROUSSE`);
          } else if (i === 5) { // 第五行逻辑
            const asciiValues = value.split("").map((char) => char.charCodeAt(0)).join(" ");
            modifiedValues.push(`Ctx_refA    =   [${asciiValues}];%Réf A ($F191)= ${value}`);
          }
        }
      } else {
        console.error(`Unsupported sheet name: ${sheetName}`);
        return;
      }

      // 将提取的值生成纯文本内容
      const textContent = modifiedValues.join("\n");

      // 根据规则生成文件名：sheetName_YYYYMMDD_HHMMSS.m
      const now = new Date();
      const timestamp = `${now.getFullYear()}${String(now.getMonth() + 1).padStart(2, "0")}${String(now.getDate()).padStart(2, "0")}_${String(now.getHours()).padStart(2, "0")}${String(now.getMinutes()).padStart(2, "0")}${String(now.getSeconds()).padStart(2, "0")}`;
      const fileName = `${sheetName}Ref_${timestamp}.m`;

      // 调用保存文件函数
      saveTextFile(textContent, fileName);
    });
  } catch (error) {
    console.error("Error exporting modified values:", error);
  }
}

// 保存纯文本文件到本地
function saveTextFile(content, fileName) {
  const blob = new Blob([content], { type: "text/plain" });
  const url = URL.createObjectURL(blob);

  // 创建一个隐藏的下载链接
  const a = document.createElement("a");
  a.href = url;
  a.download = fileName;
  document.body.appendChild(a);
  a.click(); // 触发下载
  document.body.removeChild(a);
  URL.revokeObjectURL(url); // 释放 URL 对象
}
